.switch2ON { /* Общие стили switch3 */
	position: relative;
	min-width: 100px;
	width: 100px;
	height: 50px;
	border-radius: 25px;
	display: flex;
	align-items: center;
	transition: all 0.3s ease-in-out;
	cursor: pointer;
}

	.switch2ON .circle{ /* Круг внутри переключателя */
		position: absolute;
		width: 50px;
		height: 50px;
		border-radius: 50%;
		z-index: 1;
		display: flex;
		justify-content: center;
		align-items: center;
		transition: all 0.3s ease-in-out;
		font-size: 2rem;
	}
